  font-style: normal;
  font-weight: 300;
}

html {
  font-family: Avenir, sans-serif;
}

body {
  margin: 20px 0 7px;
}

.box-shadow {
  border: 1px solid var(--border-color);
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 1px 8px #3d4e611a;
}

.search-form {
  align-items: center;
  margin: 20px 8px;
  display: flex;
}

.search-big {
  padding: 23px 25px;
}

.search-small {
