  background-size: 11px 11px;
  padding-left: 20px;
  font-size: 14px;
}

.search-form:hover {
  box-shadow: 0 4px 4px 0 var(--shadow-color);
}

.search-form:hover .search-small__input {
  text-shadow: none;
}

.search-form:focus-within {
  box-shadow: 0 4px 4px 0 var(--shadow-color);
  background: linear-gradient(#fff 0%, #f6f6f7 100%);
}

.search-form:focus-within .search-big__input {
  text-shadow: 0 4px 4px var(--shadow-color);
  font-weight: bold;
}

.search-form:has(.search-small__input) :focus-within {
  text-shadow: none;
